@import url('https://fonts.googleapis.com/css2?family=Cedarville+Cursive&family=Montserrat&family=Oswald:wght@300&display=swap');

html,
body {
    width: 100%;
    height: 100%;
}

body {
    overflow: hidden;
    background: lightblue;
}

.snow {
    color: white;
    font-size: 3em;
    .flake {
        position: fixed;
        top: -20%;
        animation: fall 10s linear infinite;
        &:nth-last-of-type(0) {
            left: 0%;
            animation-delay: 0s;
        }

        &:nth-last-of-type(1) {
            left: 0%;
            animation-delay: 0s;
        }

        &:nth-last-of-type(2) {
            left: 10%;
            animation-delay: 3s;
        }

        &:nth-last-of-type(3) {
            left: 20%;
            animation-delay: 5s;
        }

        &:nth-last-of-type(4) {
            left: 30%;
            animation-delay: 2s;
        }

        &:nth-last-of-type(5) {
            left: 40%;
            animation-delay: 1s;
        }

        &:nth-last-of-type(6) {
            left: 50%;
            animation-delay: 4s;
        }

        &:nth-last-of-type(7) {
            left: 60%;
            animation-delay: 3s;
        }

        &:nth-last-of-type(8) {
            left: 70%;
            animation-delay: 2s;
        }

        &:nth-last-of-type(9) {
            left: 80%;
            animation-delay: 0s;
        }

        &:nth-last-of-type(10) {
            left: 90%;
            animation-delay: 6s;
        }
    }
}

@keyframes fall {
    0% {
        top: -20%;
    }
    100% {
        top: 100%;
    }
}
